___CSS_LOADER_EXPORT___.push([module.id, `
/* @font-face {
  font-family: 'Inter';
  src: url('@/assets/fonts/inter.woff2') format('woff2');
}

@font-face {
  font-family: 'Roboto';
  src: url('@/assets/fonts/roboto.woff2') format('woff2');
} */

/* Chat container */
.chat-container {
  width: 1200px;
  margin: auto;
  max-width: 1200px;
  min-width: 320px;
  background: #fff;
  /* flex-direction: column; */
  /* border-radius: 10px; */
  overflow: hidden;
}
.chat-header {
  background: white;
  padding: 10px;
  color: #646464;
  font-size: 40px;
  font-family: 'Inter', sans-serif;
  text-align: center;
}

/* Chat Messages */
.chat-messages {
  height: 400px;
  overflow-y: auto;
  padding: 10px;
  background: #fff;
  color: white;
  border-radius: 5px;
  margin: 10px;
}

/* User messages */
.user-message {
  text-align: right;
  color: rgb(34, 145, 182);
  background: rgba(255, 255, 255, 0.1);
  padding: 10px;
  border-radius: 10px;
  max-width: 60%;
  margin-left: auto;
  margin-bottom: 20px;
  display: block;
  word-wrap: break-word;
  animation: fadeIn 0.2s ease-in-out;
}

/* Bot message (Markdown properly formatted) */
.bot-message {
  text-align: left;
  color: rgb(48, 154, 48);
  background: rgba(255, 255, 255, 0.1);
  padding: 12px;
  border-radius: 10px;
  max-width: 85%;
  margin-right: auto;
  display: block;
  text-align: justify;
  word-wrap: break-word;
  animation: fadeIn 0.3s ease-in-out;
}

/* Properly format Markdown inside messages */
.bot-message h1,
.bot-message h2,
.bot-message h3 {
  color: #34eb98;
  /* Green headers */
  margin-bottom: 8px;
}
.bot-message p {
  margin-bottom: 8px;
}
.bot-message ul {
  padding-left: 20px;
}
.bot-message li {
  margin-bottom: 6px;
}

/* Code blocks */
.bot-message pre {
  background: #1a1a1a;
  padding: 10px;
  border-radius: 5px;
  overflow-x: auto;
}
.bot-message code {
  color: #ffcc00;
  background: rgba(255, 255, 255, 0.1);
  padding: 2px 4px;
  border-radius: 3px;
}


/* Thinking Message Styling */
.thinking-message {
  background: #fff;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  color: #646464;
  width: fit-content;
}
.thinking-text {
  color: #87afff;
  font-style: italic;
  white-space: pre-line;
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-height: 200px;
  overflow-y: auto;
}

/*  Toggle Button */
.toggle-thinking {
  background: #fff;
  color: #646464;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 12px;
  border-radius: 8px;
  margin-bottom: 5px;
  outline: none;
  transition: all 0.3s ease-in-out;
}
.toggle-thinking:hover {
  background: #646464;
  color: #fff;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.5);
  /* Stronger shadow */
  border: none;
}
.toggle-thinking:active {
  transform: scale(0.95);
}
.toggle-thinking:focus {
  outline: none;
}

/* Chat input box */
.chat-input {
  display: flex;
  padding: 10px;
  background: #fff;
  outline: none;
  align-items: center;
  gap: 10px;
}
.chat-input textarea {
  flex-grow: 1;
  /* min-width: 200px; */
  /* max-width: 100%; */
  text-align: left;
  padding: 10px;
  border: 1px solid #646464;
  background: #fff;
  color: #646464;
  border-radius: 45px;
  outline: none;
  font-family: 'Inter', sans-serif;
  font-size: large;
  /* overflow-wrap: break-word; */
  align-content: baseline;
  resize: vertical;
  max-height: 150px;
  min-height: 43.2px;
}
.chat-input textarea:placeholder-shown {
  align-content: center;
}
.chat-input input {
  flex-grow: 1;
  padding: 12px;
  border: none;
  background: #444;
  color: white;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
}
.chat-input button {
  width: 80px;
  height: 64.8px;
  padding: 12px 20px;
  margin-left: 10px;
  background: #fff;
  color: #646464;
  border: 1px solid #646464;
  border-radius: 50px;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease-in-out;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  font-family: 'Inter', sans-serif;
}
.chat-input button:hover {
  background: #646464;
  color: #fff;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5);
}

/* Scrollbar for chat messages */
.chat-messages::-webkit-scrollbar {
  width: 8px;
}
.chat-messages::-webkit-scrollbar-track {
  background: #9d9d9d;
}
.chat-messages::-webkit-scrollbar-thumb {
  background: #717171;
  border-radius: 5px;
}
.chat-messages::-webkit-scrollbar-thumb:hover {
  background: #c2c2c2;
}

/* Fade-in animation */
@keyframes fadeIn {
from {
    opacity: 0;
    transform: translateY(5px);
}
to {
    opacity: 1;
    transform: translateY(0);
}
}

/* Responsive Design */
@media (max-width: 1320px) {
.chat-container {
    width: 900px;
}
.chat-header {
    font-size: 36px;
}
.chat-messages {
    height: 500px;
}
.chat-input input {
    padding: 10px;
}
.chat-input button {
    padding: 10px 15px;
}
}

/* Responsive Design */
@media (max-width: 1024px) {
.chat-container {
    width: 650px;
}
.chat-header {
    font-size: 30px;
}
.chat-messages {
    height: 450px;
}
.chat-input textarea {
    padding: 10px;
    font-size: medium;
}
.chat-input button {
    padding: 10px 15px;
    font-size: medium;
}
}
@media (max-width: 768px) {
.chat-container {
    width: 350px;
}
.chat-header {
    font-size: 22px;
    padding: 15px;
}
.chat-messages {
    height: 300px;
    font-size: small;
}
.chat-input input {
    padding: 8px;
    font-size: 14px;
}
.chat-input textarea {
    font-size: small;
}
.chat-input button {
    padding: 8px 12px;
    font-size: small;
}
}
@media (max-width: 480px) {
.chat-container {
    width: 100%;
    border-radius: 0;
}
.chat-header {
    font-size: 20px;
}
.chat-messages {
    height: 180px;
    padding: 5px;
}
.chat-input {
    flex-direction: column;
    gap: 5px;
}
.chat-input input {
    width: 100%;
    padding: 8px;
}
.chat-input button {
    width: 100%;
    padding: 8px;
}
}
`, "",{"version":3,"sources":["webpack://./src/components/Chatbot.vue"],"names":[],"mappings":";AAuTA;;;;;;;;GAQG;;AAEH,mBAAmB;AACnB;EACE,aAAa;EACb,YAAY;EACZ,iBAAiB;EACjB,gBAAgB;EAChB,gBAAgB;EAChB,4BAA4B;EAC5B,yBAAyB;EACzB,gBAAgB;AAClB;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,cAAc;EACd,eAAe;EACf,gCAAgC;EAChC,kBAAkB;AACpB;;AAEA,kBAAkB;AAClB;EACE,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;EAClB,YAAY;AACd;;AAEA,kBAAkB;AAClB;EACE,iBAAiB;EACjB,wBAAwB;EACxB,oCAAoC;EACpC,aAAa;EACb,mBAAmB;EACnB,cAAc;EACd,iBAAiB;EACjB,mBAAmB;EACnB,cAAc;EACd,qBAAqB;EACrB,kCAAkC;AACpC;;AAEA,8CAA8C;AAC9C;EACE,gBAAgB;EAChB,uBAAuB;EACvB,oCAAoC;EACpC,aAAa;EACb,mBAAmB;EACnB,cAAc;EACd,kBAAkB;EAClB,cAAc;EACd,mBAAmB;EACnB,qBAAqB;EACrB,kCAAkC;AACpC;;AAEA,6CAA6C;AAC7C;;;EAGE,cAAc;EACd,kBAAkB;EAClB,kBAAkB;AACpB;AAEA;EACE,kBAAkB;AACpB;AAEA;EACE,kBAAkB;AACpB;AAEA;EACE,kBAAkB;AACpB;;AAEA,gBAAgB;AAChB;EACE,mBAAmB;EACnB,aAAa;EACb,kBAAkB;EAClB,gBAAgB;AAClB;AAEA;EACE,cAAc;EACd,oCAAoC;EACpC,gBAAgB;EAChB,kBAAkB;AACpB;;;AAGA,6BAA6B;AAC7B;EACE,gBAAgB;EAChB,aAAa;EACb,cAAc;EACd,kBAAkB;EAClB,cAAc;EACd,kBAAkB;AACpB;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB,qBAAqB;EACrB,qBAAqB;EACrB,yBAAyB;EACzB,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA,mBAAmB;AACnB;EACE,gBAAgB;EAChB,cAAc;EACd,YAAY;EACZ,iBAAiB;EACjB,eAAe;EACf,eAAe;EACf,kBAAkB;EAClB,kBAAkB;EAClB,aAAa;EACb,gCAAgC;AAClC;AAEA;EACE,mBAAmB;EACnB,WAAW;EACX,gCAAgC;EAChC,yCAAyC;EACzC,oBAAoB;EACpB,YAAY;AACd;AAEA;EACE,sBAAsB;AACxB;AAEA;EACE,aAAa;AACf;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,qBAAqB;EACrB,gBAAgB;EAChB,aAAa;EACb,yBAAyB;EACzB,gBAAgB;EAChB,cAAc;EACd,mBAAmB;EACnB,aAAa;EACb,gCAAgC;EAChC,gBAAgB;EAChB,+BAA+B;EAC/B,uBAAuB;EACvB,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;AACpB;AAEA;EACE,qBAAqB;AACvB;AAEA;EACE,YAAY;EACZ,aAAa;EACb,YAAY;EACZ,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;EAClB,aAAa;EACb,eAAe;AACjB;AAEA;EACE,WAAW;EACX,cAAc;EACd,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,cAAc;EACd,yBAAyB;EACzB,mBAAmB;EACnB,eAAe;EACf,aAAa;EACb,gCAAgC;EAChC,cAAc;EACd,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;EACb,gCAAgC;AAClC;AAEA;EACE,mBAAmB;EACnB,WAAW;EACX,wCAAwC;AAE1C;;AAEA,gCAAgC;AAChC;EACE,UAAU;AACZ;AAEA;EACE,mBAAmB;AACrB;AAEA;EACE,mBAAmB;EACnB,kBAAkB;AACpB;AAEA;EACE,mBAAmB;AACrB;;AAEA,sBAAsB;AACtB;AACE;IACE,UAAU;IACV,0BAA0B;AAC5B;AAEA;IACE,UAAU;IACV,wBAAwB;AAC1B;AACF;;AAEA,sBAAsB;AACtB;AACE;IACE,YAAY;AACd;AAEA;IACE,eAAe;AACjB;AAEA;IACE,aAAa;AACf;AAEA;IACE,aAAa;AACf;AAEA;IACE,kBAAkB;AACpB;AACF;;AAEA,sBAAsB;AACtB;AACE;IACE,YAAY;AACd;AAEA;IACE,eAAe;AACjB;AAEA;IACE,aAAa;AACf;AAEA;IACE,aAAa;IACb,iBAAiB;AACnB;AAEA;IACE,kBAAkB;IAClB,iBAAiB;AACnB;AACF;AAEA;AACE;IACE,YAAY;AACd;AAEA;IACE,eAAe;IACf,aAAa;AACf;AAEA;IACE,aAAa;IACb,gBAAgB;AAClB;AAEA;IACE,YAAY;IACZ,eAAe;AACjB;AAEA;IACE,gBAAgB;AAClB;AAEA;IACE,iBAAiB;IACjB,gBAAgB;AAClB;AACF;AAEA;AACE;IACE,WAAW;IACX,gBAAgB;AAClB;AAEA;IACE,eAAe;AACjB;AAEA;IACE,aAAa;IACb,YAAY;AACd;AAEA;IACE,sBAAsB;IACtB,QAAQ;AACV;AAEA;IACE,WAAW;IACX,YAAY;AACd;AAEA;IACE,WAAW;IACX,YAAY;AACd;AACF","sourcesContent":["<template>\r\n  <div class=\"chat-container\">\r\n    <div class=\"chat-header\">How can I help?</div>\r\n\r\n    <!-- Chat Messages -->\r\n    <div class=\"chat-messages\">\r\n      <div v-for=\"(msg, index) in messages\" :key=\"index\" :class=\"msg.sender\">\r\n\r\n        <!-- User Message -->\r\n        <template v-if=\"msg.type === 'user'\">\r\n          <div class=\"message-text message user-message\">{{ msg.text }}</div>\r\n        </template>\r\n\r\n        <!-- from the web version -->\r\n        <!-- <template v-else>\r\n          <span class=\"message-text\">{{ msg.text }}</span>  this is the user message-->\r\n        <!-- </template> -->\r\n\r\n        <!-- Thinking Phase (Stored Separately) -->\r\n        <template v-if=\"msg.type === 'thinking'\">\r\n          <div class=\"thinking-message\">\r\n            <button class=\"toggle-thinking\" @click=\"toggleThinking(index)\">\r\n              {{ msg.expanded ? 'Hide Thinking' : 'Show Thinking' }}\r\n            </button>\r\n            <p v-if=\"msg.expanded\" class=\"thinking-text\">{{ msg.text }}</p>\r\n          </div>\r\n        </template>\r\n\r\n\r\n\r\n        <!-- Final Chatbot Response (Markdown Supported) -->\r\n        <template v-if=\"msg.type === 'final'\">\r\n          <div class=\"bot-message\">\r\n            <p v-if=\"msg.text.includes('```')\">\r\n              <span v-html=\"renderMarkdown(msg.text)\"></span>\r\n            </p>\r\n            <p v-else v-html=\"renderMarkdown(msg.text)\"></p>\r\n          </div>\r\n        </template>\r\n\r\n\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Chat Input Box -->\r\n    <div class=\"chat-input\">\r\n      <textarea v-model=\"userInput\" placeholder=\"Ask WeekendAI\" @keyup.enter=\"sendMessage\"\r\n        @keydown.enter.prevent=\"sendMessage\"></textarea>\r\n      <button @click=\"sendMessage\">Send</button>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n\r\n<script>\r\nimport { marked } from 'marked';\r\nconst { ipcRenderer } = window.require(\"electron\");\r\n\r\nexport default {\r\n  props: [\"model\"],\r\n  data() {\r\n    return {\r\n      userInput: \"\",\r\n      messages: [], // Stores user & final bot messages\r\n      thinkingMessages: [], // Stores separate thinking messages\r\n      finalMessageBuffer: '',\r\n      isProcessingFinal: false,\r\n    };\r\n  },\r\n  methods: {\r\n    //  Convert Markdown response to properly formatted HTML\r\n    renderMarkdown(text) {\r\n      return marked(text);\r\n    },\r\n\r\n    async sendMessage() {\r\n      if (!this.userInput.trim()) return;\r\n\r\n      const messageToSend = this.userInput;\r\n      console.log(messageToSend);\r\n      this.userInput = \"\";\r\n\r\n      // Add user message to chat\r\n      this.messages.push({ sender: 'user', text: messageToSend, type: 'user' });\r\n\r\n      let isThinking = false;\r\n      let finalIndex = -1;    // We'll create a new \"final\" message as needed\r\n      let finalResponse = \"\";\r\n      let thinkingText = \"\";\r\n      let receivedThinkTag = false;\r\n      let thinkingIndex = this.messages.length;\r\n      let thinkingAnimationActive = false;\r\n\r\n      //  Add \"Thinking...\" animation **ONLY IF DeepSeek R1 is Selected**\r\n      if (this.model.includes(\"deepseek\")) {\r\n        this.messages.push({\r\n          sender: 'bot',\r\n          type: 'thinking',\r\n          text: \"Thinking.\",\r\n          expanded: true, //  Start expanded\r\n        });\r\n\r\n        thinkingAnimationActive = true;\r\n\r\n        // Thinking animation\r\n        let dots = 0;\r\n        const thinkingAnimation = setInterval(() => {\r\n          if (!thinkingAnimationActive) {\r\n            clearInterval(thinkingAnimation); //  Stop animation when real text starts\r\n            return;\r\n          }\r\n          dots = (dots + 1) % 4;\r\n          this.messages[thinkingIndex].text = \"Thinking\" + \".\".repeat(dots);\r\n          this.$forceUpdate();\r\n        }, 500);\r\n      }\r\n\r\n\r\n      // try post request to ollama local api\r\n\r\n\r\n      try {\r\n        const response = await fetch(\"http://localhost:11434/api/generate\", {\r\n          method: \"POST\",\r\n          headers: { \"Content-Type\": \"application/json\" },\r\n          body: JSON.stringify({ model: this.model, prompt: messageToSend }),\r\n        });\r\n\r\n        const reader = response.body.getReader();\r\n        const decoder = new TextDecoder();\r\n\r\n        //  Placeholder for letter-by-letter response\r\n        // let botMessageIndex = this.messages.length;\r\n        let finalIndex = -1;\r\n\r\n        while (true) {\r\n          const { done, value } = await reader.read();\r\n          if (done) break;\r\n\r\n          const chunk = decoder.decode(value);\r\n          const lines = chunk.trim().split(\"\\n\");\r\n\r\n          for (const line of lines) {\r\n            try {\r\n              const parsed = JSON.parse(line);\r\n\r\n              if (parsed.response.includes(\"<think>\")) {\r\n                isThinking = true;\r\n                receivedThinkTag = true;\r\n                thinkingText = \"\";\r\n              }\r\n\r\n              if (isThinking) {\r\n                const snippet = parsed.response.replace(/<\\/?think>/g, \"\").trim();\r\n                if (thinkingIndex !== -1) {\r\n                  // Insert letter by letter, or word by word\r\n                  thinkingText += (thinkingText ? \" \" : \"\") + snippet;\r\n                  this.messages[thinkingIndex].text = thinkingText;\r\n                  this.$forceUpdate();\r\n                }\r\n                //  Stop \"Thinking...\" animation when real thinking text starts\r\n                if (thinkingAnimationActive) {\r\n                  thinkingAnimationActive = false;\r\n                }\r\n\r\n                // let cleanText = parsed.response.replace(/<\\/?think>/g, \"\").trim();\r\n                // let words = cleanText.split(/\\s+/);\r\n\r\n                // for (let word of words) {\r\n                //   if (word) {\r\n                //     await new Promise(resolve => setTimeout(resolve, 100));\r\n                //     thinkingText += word + \" \";\r\n                //     if (this.model.includes(\"deepseek\")) {\r\n                //       this.messages[thinkingIndex].text = thinkingText.trim();\r\n                //       this.$forceUpdate();\r\n                //     }\r\n                //   }\r\n                // }\r\n              }\r\n\r\n              if (parsed.response.includes(\"</think>\")) {\r\n                isThinking = false;\r\n\r\n                if (!thinkingText.trim()) {\r\n                  this.messages[thinkingIndex].text = \"No thinking phase for this question.\";\r\n                }\r\n                // Mark it as collapsed\r\n                this.messages[thinkingIndex].expanded = false;\r\n\r\n                if (this.model.includes(\"deepseek\")) {\r\n                  this.messages[thinkingIndex].text = thinkingText.trim();\r\n                  this.messages[thinkingIndex].expanded = false; //  Collapse, but keep it visible\r\n                }\r\n              }\r\n\r\n              // 5) If we are no longer in <think>, the text is \"final\" \r\n              // => letter by letter into a brand-new final message.\r\n              if (!isThinking) {\r\n                // Create final message if not exists\r\n                if (finalIndex === -1) {\r\n                  this.messages.push({\r\n                    sender: \"bot\",\r\n                    text: \"\",\r\n                    type: \"final\",\r\n                  });\r\n                  finalIndex = this.messages.length - 1;\r\n                }\r\n                // Now letter-by-letter in that finalIndex:\r\n                // this.displayMessageLetterByLetter(parsed.response, finalIndex);\r\n                // Append to buffer and process\r\n                this.finalMessageBuffer += parsed.response;\r\n                if (!this.isProcessingFinal) {\r\n                  this.processFinalMessage(finalIndex);\r\n                }\r\n              }\r\n\r\n              // //  If no <think> tag was detected, process response letter by letter\r\n              // if (!receivedThinkTag) {\r\n              //   this.displayMessageLetterByLetter(parsed.response, botMessageIndex);\r\n              // } else if (!isThinking) {\r\n              //   this.displayMessageLetterByLetter(parsed.response, botMessageIndex);\r\n              // }\r\n\r\n            } catch (error) {\r\n              console.error(\"Error parsing streaming data:\", error);\r\n            }\r\n          }\r\n          this.$forceUpdate();\r\n        }\r\n\r\n        //  Stop animation (but KEEP the \"Thinking...\" message visible)\r\n        // thinkingAnimationActive = false;\r\n        // // TODO\r\n        // // this.messages[thinkingIndex].text = \"\"\r\n        // if (this.model.includes(\"deepseek\")) {\r\n        //   this.messages[thinkingIndex].expanded = false; //  Collapse thinking phase but do NOT delete it\r\n        // }\r\n\r\n      } catch (error) {\r\n        console.error(\"Chatbot API error:\", error);\r\n        this.messages.push({ sender: 'bot', text: \"Error: Couldn't get a response.\", type: 'final' });\r\n      }\r\n\r\n      this.userInput = '';\r\n    }\r\n\r\n    ,\r\n\r\n    toggleThinking(index) {\r\n      if (this.messages[index]) {\r\n        this.messages[index].expanded = !this.messages[index].expanded;\r\n        this.$forceUpdate();\r\n      }\r\n    },\r\n\r\n    processFinalMessage(finalIndex) {\r\n      this.isProcessingFinal = true;\r\n      const processNextChar = () => {\r\n        if (this.finalMessageBuffer.length > 0) {\r\n          // Add first character and update\r\n          const char = this.finalMessageBuffer[0];\r\n          this.messages[finalIndex].text += char;\r\n          this.finalMessageBuffer = this.finalMessageBuffer.slice(1);\r\n          this.$forceUpdate();\r\n\r\n          // Continue processing after delay\r\n          setTimeout(processNextChar, 50);\r\n        } else {\r\n          this.isProcessingFinal = false;\r\n        }\r\n      };\r\n      processNextChar();\r\n    },\r\n\r\n    // 6) Slightly revised letter-by-letter to accept an explicit finalIndex\r\n    displayMessageLetterByLetter(text, finalIndex) {\r\n      // Just skip if empty chunk\r\n      if (!text.trim()) return;\r\n\r\n      // We'll go char-by-char or word-by-word. Let's do char-by-char:\r\n      const chars = text.split(\"\");\r\n      let i = 0;\r\n\r\n      // Make sure there's actually a final message for us\r\n      if (!this.messages[finalIndex]) {\r\n        this.messages.push({ sender: \"bot\", text: \"\", type: \"final\" });\r\n        finalIndex = this.messages.length - 1;\r\n      }\r\n\r\n      const words = text.split(/\\s+/); // Split text into words\r\n      const delay = 50; // Adjust speed for better readability\r\n      const interval = setInterval(() => {\r\n        if (index < words.length) {\r\n          this.messages[finalIndex].text += (index === 0 ? \"\" : \" \") + words[index]; // Preserve spacing\r\n          this.$forceUpdate();\r\n          index++;\r\n        } else {\r\n          clearInterval(interval);\r\n        }\r\n      }, delay);\r\n    }\r\n    ,\r\n\r\n\r\n\r\n  },\r\n};\r\n</script>\r\n\r\n\r\n<style>\r\n/* @font-face {\r\n  font-family: 'Inter';\r\n  src: url('@/assets/fonts/inter.woff2') format('woff2');\r\n}\r\n\r\n@font-face {\r\n  font-family: 'Roboto';\r\n  src: url('@/assets/fonts/roboto.woff2') format('woff2');\r\n} */\r\n\r\n/* Chat container */\r\n.chat-container {\r\n  width: 1200px;\r\n  margin: auto;\r\n  max-width: 1200px;\r\n  min-width: 320px;\r\n  background: #fff;\r\n  /* flex-direction: column; */\r\n  /* border-radius: 10px; */\r\n  overflow: hidden;\r\n}\r\n\r\n.chat-header {\r\n  background: white;\r\n  padding: 10px;\r\n  color: #646464;\r\n  font-size: 40px;\r\n  font-family: 'Inter', sans-serif;\r\n  text-align: center;\r\n}\r\n\r\n/* Chat Messages */\r\n.chat-messages {\r\n  height: 400px;\r\n  overflow-y: auto;\r\n  padding: 10px;\r\n  background: #fff;\r\n  color: white;\r\n  border-radius: 5px;\r\n  margin: 10px;\r\n}\r\n\r\n/* User messages */\r\n.user-message {\r\n  text-align: right;\r\n  color: rgb(34, 145, 182);\r\n  background: rgba(255, 255, 255, 0.1);\r\n  padding: 10px;\r\n  border-radius: 10px;\r\n  max-width: 60%;\r\n  margin-left: auto;\r\n  margin-bottom: 20px;\r\n  display: block;\r\n  word-wrap: break-word;\r\n  animation: fadeIn 0.2s ease-in-out;\r\n}\r\n\r\n/* Bot message (Markdown properly formatted) */\r\n.bot-message {\r\n  text-align: left;\r\n  color: rgb(48, 154, 48);\r\n  background: rgba(255, 255, 255, 0.1);\r\n  padding: 12px;\r\n  border-radius: 10px;\r\n  max-width: 85%;\r\n  margin-right: auto;\r\n  display: block;\r\n  text-align: justify;\r\n  word-wrap: break-word;\r\n  animation: fadeIn 0.3s ease-in-out;\r\n}\r\n\r\n/* Properly format Markdown inside messages */\r\n.bot-message h1,\r\n.bot-message h2,\r\n.bot-message h3 {\r\n  color: #34eb98;\r\n  /* Green headers */\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.bot-message p {\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.bot-message ul {\r\n  padding-left: 20px;\r\n}\r\n\r\n.bot-message li {\r\n  margin-bottom: 6px;\r\n}\r\n\r\n/* Code blocks */\r\n.bot-message pre {\r\n  background: #1a1a1a;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  overflow-x: auto;\r\n}\r\n\r\n.bot-message code {\r\n  color: #ffcc00;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  padding: 2px 4px;\r\n  border-radius: 3px;\r\n}\r\n\r\n\r\n/* Thinking Message Styling */\r\n.thinking-message {\r\n  background: #fff;\r\n  padding: 10px;\r\n  margin: 10px 0;\r\n  border-radius: 5px;\r\n  color: #646464;\r\n  width: fit-content;\r\n}\r\n\r\n.thinking-text {\r\n  color: #87afff;\r\n  font-style: italic;\r\n  white-space: pre-line;\r\n  word-wrap: break-word;\r\n  overflow-wrap: break-word;\r\n  max-height: 200px;\r\n  overflow-y: auto;\r\n}\r\n\r\n/*  Toggle Button */\r\n.toggle-thinking {\r\n  background: #fff;\r\n  color: #646464;\r\n  border: none;\r\n  padding: 5px 10px;\r\n  cursor: pointer;\r\n  font-size: 12px;\r\n  border-radius: 8px;\r\n  margin-bottom: 5px;\r\n  outline: none;\r\n  transition: all 0.3s ease-in-out;\r\n}\r\n\r\n.toggle-thinking:hover {\r\n  background: #646464;\r\n  color: #fff;\r\n  transition: all 0.3s ease-in-out;\r\n  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.5);\r\n  /* Stronger shadow */\r\n  border: none;\r\n}\r\n\r\n.toggle-thinking:active {\r\n  transform: scale(0.95);\r\n}\r\n\r\n.toggle-thinking:focus {\r\n  outline: none;\r\n}\r\n\r\n/* Chat input box */\r\n.chat-input {\r\n  display: flex;\r\n  padding: 10px;\r\n  background: #fff;\r\n  outline: none;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.chat-input textarea {\r\n  flex-grow: 1;\r\n  /* min-width: 200px; */\r\n  /* max-width: 100%; */\r\n  text-align: left;\r\n  padding: 10px;\r\n  border: 1px solid #646464;\r\n  background: #fff;\r\n  color: #646464;\r\n  border-radius: 45px;\r\n  outline: none;\r\n  font-family: 'Inter', sans-serif;\r\n  font-size: large;\r\n  /* overflow-wrap: break-word; */\r\n  align-content: baseline;\r\n  resize: vertical;\r\n  max-height: 150px;\r\n  min-height: 43.2px;\r\n}\r\n\r\n.chat-input textarea:placeholder-shown {\r\n  align-content: center;\r\n}\r\n\r\n.chat-input input {\r\n  flex-grow: 1;\r\n  padding: 12px;\r\n  border: none;\r\n  background: #444;\r\n  color: white;\r\n  border-radius: 5px;\r\n  outline: none;\r\n  font-size: 16px;\r\n}\r\n\r\n.chat-input button {\r\n  width: 80px;\r\n  height: 64.8px;\r\n  padding: 12px 20px;\r\n  margin-left: 10px;\r\n  background: #fff;\r\n  color: #646464;\r\n  border: 1px solid #646464;\r\n  border-radius: 50px;\r\n  cursor: pointer;\r\n  outline: none;\r\n  transition: all 0.3s ease-in-out;\r\n  flex-shrink: 0;\r\n  justify-content: center;\r\n  align-items: center;\r\n  display: flex;\r\n  font-family: 'Inter', sans-serif;\r\n}\r\n\r\n.chat-input button:hover {\r\n  background: #646464;\r\n  color: #fff;\r\n  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5);\r\n\r\n}\r\n\r\n/* Scrollbar for chat messages */\r\n.chat-messages::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n.chat-messages::-webkit-scrollbar-track {\r\n  background: #9d9d9d;\r\n}\r\n\r\n.chat-messages::-webkit-scrollbar-thumb {\r\n  background: #717171;\r\n  border-radius: 5px;\r\n}\r\n\r\n.chat-messages::-webkit-scrollbar-thumb:hover {\r\n  background: #c2c2c2;\r\n}\r\n\r\n/* Fade-in animation */\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(5px);\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n/* Responsive Design */\r\n@media (max-width: 1320px) {\r\n  .chat-container {\r\n    width: 900px;\r\n  }\r\n\r\n  .chat-header {\r\n    font-size: 36px;\r\n  }\r\n\r\n  .chat-messages {\r\n    height: 500px;\r\n  }\r\n\r\n  .chat-input input {\r\n    padding: 10px;\r\n  }\r\n\r\n  .chat-input button {\r\n    padding: 10px 15px;\r\n  }\r\n}\r\n\r\n/* Responsive Design */\r\n@media (max-width: 1024px) {\r\n  .chat-container {\r\n    width: 650px;\r\n  }\r\n\r\n  .chat-header {\r\n    font-size: 30px;\r\n  }\r\n\r\n  .chat-messages {\r\n    height: 450px;\r\n  }\r\n\r\n  .chat-input textarea {\r\n    padding: 10px;\r\n    font-size: medium;\r\n  }\r\n\r\n  .chat-input button {\r\n    padding: 10px 15px;\r\n    font-size: medium;\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .chat-container {\r\n    width: 350px;\r\n  }\r\n\r\n  .chat-header {\r\n    font-size: 22px;\r\n    padding: 15px;\r\n  }\r\n\r\n  .chat-messages {\r\n    height: 300px;\r\n    font-size: small;\r\n  }\r\n\r\n  .chat-input input {\r\n    padding: 8px;\r\n    font-size: 14px;\r\n  }\r\n\r\n  .chat-input textarea {\r\n    font-size: small;\r\n  }\r\n\r\n  .chat-input button {\r\n    padding: 8px 12px;\r\n    font-size: small;\r\n  }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n  .chat-container {\r\n    width: 100%;\r\n    border-radius: 0;\r\n  }\r\n\r\n  .chat-header {\r\n    font-size: 20px;\r\n  }\r\n\r\n  .chat-messages {\r\n    height: 180px;\r\n    padding: 5px;\r\n  }\r\n\r\n  .chat-input {\r\n    flex-direction: column;\r\n    gap: 5px;\r\n  }\r\n\r\n  .chat-input input {\r\n    width: 100%;\r\n    padding: 8px;\r\n  }\r\n\r\n  .chat-input button {\r\n    width: 100%;\r\n    padding: 8px;\r\n  }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
