`, "",{"version":3,"sources":["webpack://./src/components/model-selection/ModelSelection.vue"],"names":[],"mappings":";AA8EA;EACE,4BAA4B;AAC9B","sourcesContent":["<script>\nimport {\n  selectedModel,\n  selectedStage,\n  models,\n  modelSelectCollapsed,\n  toggleDropdown,\n  selectModel,\n  dropdownPosition,\n} from \"./modelselect-state.js\";\nimport {\n  ref,\n  computed\n} from 'vue';\n\nexport default {\n  setup() {\n\n    const groupedModels = computed(() => {\n      return models.reduce((acc, model) => {\n        if (!acc[model.stage]) acc[model.stage] = [];\n        acc[model.stage].push(model);\n        return acc;\n      }, {});\n    });\n\n    return {\n      selectedModel,\n      selectedStage,\n      models,\n      modelSelectCollapsed,\n      toggleDropdown,\n      selectModel,\n      dropdownPosition,\n      groupedModels\n    };\n  },\n};\n</script>\n\n<template>\n  <div class=\"dropdown-container\" :style=\"{ left: dropdownPosition }\">\n    <button class=\"dropdown-button\" @click=\"toggleDropdown\">\n      <div class=\"model-info\">\n        <span class=\"model-name\">{{ selectedModel }}</span>\n        <span class=\"model-stage\">{{ selectedStage }}</span>\n      </div>\n      <span class=\"dropdown-arrow\">\n        <i class=\"fa-solid fa-chevron-down\"></i>\n      </span>\n    </button>\n\n    <!-- Dropdown Menu -->\n    <div v-if=\"!modelSelectCollapsed\" class=\"dropdown-menu\">\n      <div v-for=\"(stage, stageName) in groupedModels\" :key=\"stageName\">\n        <div class=\"dropdown-stage\">{{ stageName }}</div>\n        <div v-for=\"model in stage\" :key=\"model.model\" @click=\"selectModel(model.model)\" class=\"dropdown-item\"\n          :class=\"{ active: model.model === selectedModel }\">\n          {{ model.model }}\n        </div>\n      </div>\n    </div>\n    <!-- old code for dropdown menu -->\n    <!-- <div v-if=\"!modelSelectCollapsed\" class=\"dropdown-menu\">\n      <div\n        v-for=\"(item, index) in models\"\n        :key=\"index\"\n        class=\"dropdown-item\"\n        @click=\"selectModel(item.model)\"\n      >\n        {{ item.model }}\n      </div>\n    </div> -->\n  </div>\n</template>\n\n\n<style>\n:root {\n  --dropdown-bg-color: #c5e0f3;\n}\n</style>\n\n<style scoped>\n/* Dropdown Container */\n.dropdown-container {\n  position: absolute;\n  top: 20px;\n  display: flex;\n  align-items: center;\n  transition: 0.3s ease;\n}\n\n/* End Dropdown Container */\n\n.dropdown-stage {\n  font-size: 14px;\n  font-weight: bold;\n  margin-top: 1px;\n  margin-bottom: 1px;\n  color: #646464;\n  text-align: left;\n  padding-left: 5px;\n}\n\n/* Dropdown Button */\n.dropdown-button {\n  background-color: white;\n  display: flex;\n  align-items: center;\n  text-align: left;\n  justify-content: space-between;\n  width: 175px;\n  cursor: pointer;\n  outline: none;\n}\n\n.dropdown-button:hover {\n  background-color: rgba(0, 0, 0, 0.1);\n  outline: none;\n  border: 1px solid transparent;\n}\n\n.dropdown-arrow {\n  color: #21262f;\n}\n\n.model-info {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n}\n\n.model-name {\n  font-size: 18px;\n  color: #646464;\n}\n\n.model-stage {\n  font-size: 13px;\n  justify-content: space-between;\n  color: #646464;\n}\n\n/* End Dropdown Button */\n\n/* Dropdown menu */\n.dropdown-menu {\n  position: absolute;\n  top: 100%;\n  left: 0;\n  width: 220px;\n  background-color: var(--dropdown-bg-color);\n  border-radius: 8px;\n  padding: 8px 0;\n  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);\n  z-index: 10;\n}\n\n.dropdown-item {\n  padding-left: 12px;\n  cursor: pointer;\n  border-radius: 6px;\n  transition: background 0.2s;\n  font-size: 16px;\n  color: #646464;\n  text-align: left;\n}\n\n.dropdown-item:hover {\n  /* background-color: rgba(0, 0, 0, 0.1); */\n  background: #a8d0e6;\n}\n\n/* End Dropdown menu */\n</style>\n"],"sourceRoot":""}]);
